.job-header {
    
}

.box-job{
    padding-left: 22px;
    width: 550px;
    
}
.timeline{
    
    border-left: 2px solid #e0e0e0;
    padding: 15px 0;
}
.ui.header.line-ribbon{
    margin-top: -20px;
}
.center-timeline{
    width: 580px;
    margin-left: auto!important;
    margin-right: auto!important;
}

.raised::before{
    content: '';
    display: inline-block;
    position: absolute;
    background-color: #fff;
    border-radius: 10px;
    width: 12px;
    height: 12px;
    top: 20px;
    left: -29px;
    border: 2px solid #2eca7f;
    z-index: 2;
}
.raised::after{
    content: '';
    display: inline-block;
    position: absolute;
    background-color: #2eca7f;
    border-radius: 10px;
    width: 29px;
    height: 2px;
    top: 25px;
    left: -29px;
    z-index: 1;
}


