.background-skill{
    background-color:white;
}
.h1skill{
    padding-top: 40px;
    padding-bottom: 40px;
}
.fa-code{
    padding-top: 23px;
    padding-left: 24px;
    padding-right: 24px;
    padding-bottom: 25px;
    font-size: 44px;
}
.fa-magic{
    padding-top: 27px;
    padding-right: 23px;
    padding-left: 27px;
    padding-bottom: 23px;
    font-size: 44px;
}
.fa-code-fork{
    padding-top: 22px;
    padding-right: 23px;
    padding-left: 28px;
    padding-bottom: 23px;
    font-size: 50px;
}
.fa-cogs{
    padding-top: 26px;
    padding-right: 24px;
    padding-left: 24px;
    padding-bottom: 23px;
    font-size: 44px;
}

.circle-skill{
    
    border: 2.5px solid rgb(241, 177, 58);    
    height: 100px;
    width:100px;
    border-radius: 50%;
    -moz-border-radius: 50%;
    -webkit-border-radius: 50%;
    background-color: wheat;
    
}

.border-skill{
    padding-top: 90px;
    margin-top: -56px;
    padding-bottom: 56px;
    border: 2.5px solid rgb(252, 200, 123);
}
.left {
    text-align: left;
    padding-left: 15px;
}

.bar-right{
    margin-top: 40px;
    margin-bottom: 40px;
    margin-right: 0px;
    margin-left: auto;
    width: 400px;
    padding-top: 8px;
    padding-right: 210px;
    border-radius: 25px 0 0 25px;
    background-image: -webkit-linear-gradient(309deg,  #2cb5e8 0%, #1fc8db 51%, #0fb8ad 75%);
    background-image: -o-linear-gradient(309deg,  #2cb5e8 0%, #1fc8db 51%, #0fb8ad 75%);
    background-image: linear-gradient(141deg,  #2cb5e8 0%, #1fc8db 51%, #0fb8ad 75%);
    padding-bottom: 8px;
  
}

@media only screen and (max-width: 500px){
    .h1skill{
        padding-top: 30px;
        padding-top: 30px;
    }
}


