
.position-img{
    margin-left: auto;
    height: 270px;
}
.segment-aboutme{
    margin-top: 40px;
}
.padding-end{
    padding-bottom: 40px;
}
.inline{
    display: inline;
    font-size: 1.9rem;
    color: white;
}
.paint-word{
  
}

.bar-left{
    height: 50px;
    width: 400px;
    padding-top: 6px;
    padding-left: 200px;
    border-radius: 0 25px 25px 0;
    background-image: -o-linear-gradient(342deg,#e21517 0,#f3c60d 100%);
    background-image: -webkit-linear-gradient(309deg, #0fb8ad 0%, #1fc8db 51%, #2cb5e8 75%);
    background-image: -o-linear-gradient(309deg, #0fb8ad 0%, #1fc8db 51%, #2cb5e8 75%);
    background-image: linear-gradient(141deg, #0fb8ad 0%, #1fc8db 51%, #2cb5e8 75%);
    
}

@media only screen and (max-width: 500px){
    .segment-boxtext {
        min-height: 350px;
    }
    .segment-end{
        min-height: 400px;
    }
    .position-img{
        margin-right: auto;
        margin-left: auto;
        height: 150px;
    }
    .segment-aboutme{
        margin-top: 30px;
    }
    .ui.medium.image{
        width: 150px;
        height: auto;
    }
    .padding-end{
        padding-bottom: 40px;
    }
}




