.segment-top{
    margin-top: 1.5em;
}
.welcome-landing{
   /* background-image: url(./img/11247.png);
   
    background-size: 120% 100%;
    background-repeat: no-repeat;
    background-position: right;
    */
    background: -webkit-linear-gradient(309deg, #0fb8ad 0%, #1fc8db 51%, #2cb5e8 75%);
    background: -o-linear-gradient(309deg, #0fb8ad 0%, #1fc8db 51%, #2cb5e8 75%);
    background: linear-gradient(141deg, #0fb8ad 0%, #1fc8db 51%, #2cb5e8 75%);
    background-size: 100%;
    color: white;
   
}
.center{
    text-align: center;
}
.segment-bottom{
    min-height: 730px;
}
.brand-heading{
    font-size: 3.7rem;
}
.intro-text{
    font-size: 2.5rem;
}
.border-hello{
    padding-top: 30px;
    padding-bottom: 30px;
    border-width: 5px;
    border-style: double;
    margin-top: 45px;
}



@media only screen and (max-width: 500px) {
    .segment-top{
        margin-top: 0.8em;
    }
    .welcome-landing{
        background: -webkit-linear-gradient(309deg, #0fb8ad 0%, #1fc8db 51%, #2cb5e8 75%);
        background: -o-linear-gradient(309deg, #0fb8ad 0%, #1fc8db 51%, #2cb5e8 75%);
        background: linear-gradient(141deg, #0fb8ad 0%, #1fc8db 51%, #2cb5e8 75%);
        background-size: cover;
    }
    .center{
        text-align: center;
    }
    .segment-bottom{
        min-height: 530px;
    }
    .brand-heading{
        font-size: 1.7rem;
    }
    .intro-text{
        font-size: 1.2rem;
    }
    .border-hello{
        
        margin-top: 30px;
    }
}

@-webkit-keyframes spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }

@keyframes spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }



